@import '../src/assets/includes/includes.scss';
// Basic reset for CSS
*,
*::after,
*::before{
box-sizing: border-box;
}
*{
  margin: 0;
}
html {
  color-scheme: light dark;
}
img{
  max-width: 100%;
  display: block;
  object-fit: cover;
}
body{
  background-color: var(--clr-background);
  user-select: none;
}


h1{
  color:var(--clr-heading-text);
}
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--ff-font-heading);
  color:var(--clr-text);
  text-wrap: balance;
}
p{
  font-family: var(--fs-font);
  text-align: justify;
}

h1 {
  font-size: var(--step-5);
}

h2 {
  font-size: var(--step-4);
}

h3 {
  font-size: var(--step-3);
}

h4 {
  font-size: var(--step-2);
}

h5 {
  font-size: var(--step-1);
}
h6 {
  font-size: var(--step-0);
}
p{
  font-size: var(--step--1);
}
.icon {
  width: 50px;
  height: auto;
  fill: var(--clr-icon,var(--clr-primary)) !important;
  stroke-width: 1 !important;
  color:  var(--clr-icon,var(--clr-primary)) !important;
}

